import Link from 'next/link'
import React, { Component } from 'react'
import 'isomorphic-fetch'

import Layout from '../components/layout.js'
import css from '../style.css'
import Category from '../components/Category.js'
const API = 'http://localhost:4000/graphql'

export default class ManagementSystem extends Component {
  static async getInitialProps() {
    const query = `{
      allCategories {
        id
        name
        projects {
          title
          description
          id
        }
      }
    }`
    const res = await fetch(API, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      },
      body: JSON.stringify({ query })
    })

    const { data } = await res.json()

    return data
  }

  constructor() {
    super()
    this.state = {
      categories: []
    }
  }

  componentWillMount = () => {
    this.setState({ categories: this.props.allCategories })
  }

  // dragStart = (e, draggedProject, fromCategoryId) => {
  //   e.dataTransfer.setData('id', draggedProject.id)
  //   this.setState({ draggedProject, fromCategoryId })
  // }

  onDrop = (e, targetCategoryId) => {
    const fromCategoryId = e.dataTransfer.getData('fromCategoryId')

    const draggedProject = JSON.parse(e.dataTransfer.getData('draggedProject'))

    const categories = this.state.categories.slice()
    if (targetCategoryId === fromCategoryId) return

    // To category..
    let targetCategory = []
    let targetCategoryIndex = -1
    categories.forEach((category, index) => {
      if (category.id === targetCategoryId) {
        targetCategory = category
        targetCategoryIndex = index
      } else if (fromCategoryId === category.id) {
        category.projects = category.projects.filter(
          project => project.id !== draggedProject.id
        )
      }
    })

    targetCategory.projects.push(draggedProject)
    categories.splice(targetCategoryIndex, 1, targetCategory)
    // this.setState({ categories })

    // this.updateDB({ fromCategoryId, toCategoryId: id, projectId })
  }

  updateDB = async ({ fromCategoryId, toCategoryId, projectId }) => {
    const query = `{
      mutation {
        moveProject(fromCategoryId: ${fromCategoryId}, toCategoryId: ${toCategoryId}, projectId: ${projectId}) {
          id
          name
          projects {
            title
            description
            id
          }
        }
      }
    }`

    // fetch(API, {
    //   method: 'POST'
    // })

    console.log(this.state)
  }

  removeProject = id => {
    console.log('Remove: ', id)
  }

  render() {
    // console.log('the state:', this.state)
    const { categories } = this.state

    const allCategories = categories.map(category => (
      // <div key={category.id} className={css.item}>
      // </div>
      <Category key={category.id} category={category} onDrop={this.onDrop} />
    ))

    return (
      <Layout>
        <div className={css.container}>{allCategories}</div>
      </Layout>
    )
  }
}

